<template>
  <div class="wrap">
    <div style="position:fixed;right:0;top:50%;z-index:100">{{ current }}</div>
    <swiper :slidesPerView="'auto'" :preventClicks="true" :freeMode="true" class="snbSwiper" @slideChange="onChange"
      ref="mySwiper">
      <swiper-slide v-for="n in array" :key="n.id" :class="{ 'on': n.id === current }">
        <a :href="`#${n.id}`" @click.stop.prevent="clickSlide($event)">{{ n.txt }}</a>
      </swiper-slide>
    </swiper>
    <div :ref="i.id" class="section" v-for="i in items" :key="i.id">{{ i.txt }}</div>
    <div style="position:fixed;bottom:0;left:0;background:skyblue;width:100%;height:50vh;z-index:100">
      <div id="mydivheader">Click here to move</div>
    </div>
  </div>
  
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper-vue2'
import 'swiper/swiper-bundle.css'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      current: 'strawberry',
      array: [{
        id: 'grape',
        txt: '포도'
      },
      {
        id: 'blueberry',
        txt: '블루베리'
      },
      {
        id: 'strawberry',
        txt: '딸기'
      },
      {
        id: 'plum',
        txt: '자두'
      },
      {
        id: 'watermelone',
        txt: '수박'
      },
      {
        id: 'pear',
        txt: '배'
      },
      {
        id: 'orange',
        txt: '오렌지'
      },
      {
        id: 'shinemusket',
        txt: '샤인머스켓'
      },
      ],
      items: [],
      swiper: null,
      slides: null,
      timer: null,
    }
  },
  created() {
    for (const element of this.array) {
      this.items.push({
        id: element.id,
        txt: element.txt
      })
    }
  },
  watch: {
    current(value) {
      this.moveSlides(value)
    }
  },
  mounted() {
    this.swiper = this.$refs.mySwiper.$swiper;
    this.onChange(this.current)
    window.addEventListener('scroll', this.onScroll)

    this.dragElement(document.getElementById("mydiv"));
  },
  methods: {
    clickSlide(e) {
      this.onChange(e.currentTarget.hash.split('#')[1])
    },
    onChange(value) {
      this.current = value
      this.scrollY()
    },
    onScroll() {
      const st = window.scrollY + window.innerHeight / 2
      Object.keys(this.$refs).forEach((id) => {
        if (st >= this.$refs[id][0]?.offsetTop) {
          this.current = id
        }
      })
    },
    scrollY() {
      window.removeEventListener('scroll', this.onScroll)
      const y = this.$refs[this.current][0]?.offsetTop - 48
      window.scroll({
        top: y,
        left: 0,
        behavior: 'smooth'
      })
      window.addEventListener('scroll', this.addEventScroll());
    },
    addEventScroll() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        window.addEventListener('scroll', this.onScroll)
      }, 1000);
    },
    moveSlides(value) {
      const slides = document.querySelectorAll('.snbSwiper .swiper-slide')
      let slide
      Object.keys(this.$refs).forEach((id, idx) => {
        if (id === value) {
          slide = slides[idx - 1]
        }
      })
      let sumSlideWidth = 0
      slides.forEach((s) => (sumSlideWidth += Number(s.offsetWidth)))

      const wrapperWidth = slide.parentNode.offsetWidth
      const slidePos = slide.offsetLeft + slide.offsetWidth / 2
      const centerPos = wrapperWidth / 2

      let pos = 0
      if (sumSlideWidth < wrapperWidth || slidePos <= centerPos) {
        pos = 0
      } else if (sumSlideWidth - slidePos <= centerPos) {
        pos = sumSlideWidth - wrapperWidth
      } else {
        pos = slidePos - centerPos
      }
      slide.parentNode.parentNode.scroll({
        top: 0,
        left: pos,
        behavior: 'smooth'
      })
    },


    dragElement(elmnt) {
      var pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;
      if (document.getElementById(elmnt.id + "header")) {
        // if present, the header is where you move the DIV from:
        document.getElementById(elmnt.id + "header").onmousedown = dragMouseDown;
      } else {
        // otherwise, move the DIV from anywhere inside the DIV:
        elmnt.onmousedown = dragMouseDown;
      }

      function dragMouseDown(e) {
        e = e || window.event;
        e.preventDefault();
        // get the mouse cursor position at startup:
        pos3 = e.clientX;
        pos4 = e.clientY;
        document.onmouseup = closeDragElement;
        // call a function whenever the cursor moves:
        document.onmousemove = elementDrag;
      }

      function elementDrag(e) {
        e = e || window.event;
        e.preventDefault();
        // calculate the new cursor position:
        pos1 = pos3 - e.clientX;
        pos2 = pos4 - e.clientY;
        pos3 = e.clientX;
        pos4 = e.clientY;
        // set the element's new position:
        elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
        elmnt.style.left = (elmnt.offsetLeft - pos1) + "px";
      }

      function closeDragElement() {
        // stop moving when mouse button is released:
        document.onmouseup = null;
        document.onmousemove = null;
      }
    }

  }
}
</script>

<style>
.wrap {
  overflow: hidden;
  max-width: 360px;
  margin: 0 auto;
  background: orchid;
}

.snbSwiper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 360px;
  box-sizing: border-box;
  background: powderblue;
}

.swiper-slide {
  width: auto !important;
}

.swiper-slide a {
  display: block;
  text-align: center;
  padding: 16px 10px 8px;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  height: 47px;
  white-space: nowrap;
  font-size: 1rem;
  line-height: 1em;
  color: #333;
}

.on {
  color: #1b64bc;
  font-weight: bold;
}

.on:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #1b64bc;
}

.section {
  margin-top: 50px;
  height: 1000px;

}
</style>