<template>
  <div class="wrap">
    <!-- https://www.w3schools.com/howto/howto_js_draggable.asp -->
    <div id="mydiv">
      <div id="mydivheader">Click here to move</div>
      진짜 짜증나
    </div>
  </div>
</template>

<script>
export default {
  name: 'DragModal',
  data() {
    return {
      pos2 : 0, 
      pos4 : 0,
      elmnt: null,
    }
  },
  mounted() {
    this.elmnt = document.getElementById("mydiv");
    this.elmnt.addEventListener('mousedown', (e) => (e.target.id === 'mydivheader') && this.dragMouseDown(e));
  },
  methods: {
    dragMouseDown(e) {
      e = e || window.event;
      e.stopPropagation();
      this.pos4 = e.clientY;
      window.addEventListener('mouseup', this.closeDragElement);
      window.addEventListener('mousemove', this.elementDrag);
    },
    elementDrag(e) {
      e = e || window.event;
      e.stopPropagation();
      this.pos2 = this.pos4 - e.clientY;
      this.pos4 = e.clientY;
      if(this.pos2 < 0) {
        this.elmnt.style.top = (this.elmnt.offsetTop - this.pos2) + "px";
      }
    },
    closeDragElement() {
      window.removeEventListener('mouseup', this.closeDragElement);
      window.removeEventListener('mousemove', this.elementDrag);
    }
  }
}
</script>
<style>
#mydiv {
  position: absolute;
  background-color: #f1f1f1;
  text-align: center;
  border: 1px solid #d3d3d3;
}

#mydivheader {
  position: relative;
  padding: 10px;
  cursor: move;
  background-color: #2196F3;
  color: #fff;
}
</style>